    }

    .full-tile h2 {
        margin: 0;
        /* font-size: 30px; */
        font-size: 4vw;
        font-weight: 400;
        margin-left: 1vw;
    }

    .full-tile h3 {
        margin: 0;
        /* font-size: 20px; */
        font-size: 3vw;
        font-weight: 200;
    }

    .full-tile p {
        font-size: 2.7vw;
        font-weight: 200;
        white-space: pre-wrap;
        padding-left: 30px;
